// helper class & functional class
.center {
    display: flex;
    justify-content: center;
}

// style for main framework
body {
    background-color: #f5f5f5;
}

.pure-g [class*=pure-u] {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji" !important;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    color: rgb(33, 37, 41);

    code {
        font-family: ui-monospace, "Cascadia Mono", "Segoe UI Mono", "Liberation Mono", Menlo, Monaco, Consolas, "Microsoft YaHei", monospace;
    }

    :not(pre)>code {
        font-size: 14px;
        background-color: #e3e6e8;
        padding: 2px 4px;
        border-radius: 3px;
    }

    .highlight {
        padding: 0;
        width: 100%;
        font-size: 13px;
        margin-bottom: 16px;
        overflow-x: auto;

        pre {
            margin: 0;
            padding: 0;
        }
    }

    blockquote {
        position: relative;
        overflow: hidden;
        margin: 0 16px 16px 16px;
        padding: 16px 0 0 16px;
        color: #525960;

        &::before {
            content: "";
            display: block;
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            width: 4px;
            border-radius: 2px;
            background: #c8ccd0;
        }

        mark {
            // fix color for mark inside blockquote
            color: inherit;
        }
    }

    overflow-wrap: break-word;

    summary {
        margin-bottom: 4px;
    }

    details {
        margin-bottom: 16px;
    }
}

.page {
    max-width: 1150px;
    margin: auto;
    background-color: white;
}

.container {
    max-width: 760px;
    margin: auto;
}

.moment-content {
    position: relative;
}

.moment-row {
    margin-bottom: 20px;
}

@media (min-width:1150px) {
    .page {
        margin-top: 50px;
    }
}

@media (max-width: 720px) {
    .moment-row {
        margin-bottom: 20px;
    }

    .container {
        padding-left: 10px;
        padding-right: 10px;
    }
}

p {
    margin-top: 0;
}

/* START - top cover part */
.jumbotron {
    padding-top: 64px;
    background-position: center;
    background-size: cover;
    min-height: 200px;
    text-align: center;
    margin-bottom: 30px;

    .site-title {
        color: rgb(108, 117, 125);
        font-size: 56px;
        font-weight: 300;
        margin-bottom: 20px;
    }

    .signature {
        color: rgba(0, 0, 0, 0.5);
        font-size: 20px;
        font-weight: 300;
    }
}

/* END - top cover part */

/* START - pagination */
// todo replace this part
$dark-blue: #21365c;
$blue: #3A6bA5;
$gray: #919EB1;

.pagination {
    display: flex;
    margin-top: 20px;
    margin-bottom: 30px;

    .page-item {
        padding-left: 15px;
        padding-right: 15px;
        padding-top: 10px;
        padding-bottom: 10px;

        &.active {
            background-color: $blue;
            color: #ffffff;
        }

        &.disabled {
            color: $gray;
        }
    }

    a {
        color: $dark-blue;
        text-decoration: none;
    }

    .page-link .page-item:hover {
        background-color: #f5f5f5;
    }
}

/* END - pagination */


/* START - time */
.time {
    text-align: right;
    padding-right: 60px;
    /* white-space: nowrap; */
    font-weight: 300;
    margin-bottom: 20px;

    .month-day {
        font-size: 24px;
        margin-bottom: 10px;
    }

    .year {
        font-size: 16px;
    }
}

@media (max-width: 576px) {
    .time {
        text-align: center;
        padding-right: 15px;
    }
}

@media (max-width: 720px) and (min-width:576px) {
    .time {
        padding-right: 20px;
    }
}

/* END - time */


/* START - user name, avatar & tags */
.user-info {
    display: flex;

    .avatar {
        max-height: 45px;
        float: left;
    }

    .intro {
        float: left;
        padding-left: 5px;

        .name {
            color: #186D9C !important;
            margin-bottom: 0;
            line-height: 1.1;
            font-size: 24px;
        }

        div:nth-child(2) {
            word-break: break-all;
            line-height: 18px;
        }

        .tag {
            font-size: 80%;
            margin-left: 0px;
            margin-right: 10px;
            color: #6c757d !important;
            white-space: nowrap;

            &::before {
                content: "#";
                padding-right: 1px;
            }
        }
    }

    margin-bottom: 24px;
}

/* END - user name, avatar & tags */


/* START - for the main content */
.context {
    clear: left;
    margin-bottom: 10px;

    a {
        text-decoration: none;
        color: #0059b3;

        &:visited {
            color: #0059b3;
        }

        &::after {
            padding-left: 3px;

            font-family: "Font Awesome 5 Free";
            content: "\f35d";
            font-weight: 900;
            display: inline-block;
            font-style: normal;
            font-variant: normal;
            text-rendering: auto;
            -webkit-font-smoothing: antialiased;
        }
    }
}

.single-pic {
    max-width: 65%;
    max-height: 250px;
    padding-bottom: 10px;
    margin-top: 5px;

    &:hover {
        cursor: pointer;
    }
}

// pic set
.pic-grid {
    max-width: 80%;

    .img-container {
        padding-top: 50%;
        padding-bottom: 50%;
        position: relative;
        height: 0px;
    }

    img {
        object-fit: cover;
        height: 100%;
        width: 100%;
        position: absolute;
        top: 0
    }

    .add-padding {
        padding: 2.5px;
    }

    &:hover {
        cursor: pointer;
    }
}

/* for links */
.share-link-block {
    padding-bottom: 10px;
    padding-top: 10px;
    margin-bottom: 10px;
    background: #f7f7f7;

    .link-table {
        margin-left: 10%;
        max-width: 80%;
    }

    .link-logo {
        max-width: 50px;
        max-height: 50px;
    }

    a {
        color: #17a2b8;
        word-wrap: break-word;
        text-decoration: none;
    }
}

.bilibili-video {
    position: relative;
    max-width: 85%;
    height: 0;
    padding-bottom: 45%;
    margin-bottom: 10px;

    iframe {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
    }
}

// for the note info
.moment-note {
    display: flex;
    margin-bottom: 10px;
    justify-content: space-between;
    margin-right: 5px;
    color: #808080;

    .note {
        font-size: 80%;
        text-indent: 20px;
        color: rgba(0, 0, 0, 0.5);

        margin-top: 0;
        margin-bottom: 0;
    }
}

// note at the bottom of each row
.moment-note {
    .like-btn {
        padding-right: 12px;

        &:hover {
            cursor: pointer;
        }
    }

    .comment-btn {
        padding-right: 10px;

        &:hover {
            cursor: pointer;
        }
    }
}

