/* END - for the main content */


/* START - footer*/
.footer {
    padding-top: 20px;
    padding-bottom: 30px;
    background-color: #f5f5f5;

    color: #6c757d !important;
    text-align: center;
    font-size: 95%;

    a {
        text-decoration: underline !important;
        color: #6c757d !important;
    }
}

/* END - footer*/


/* back-to-top button */
#back-top-button {
    display: none;
    position: fixed;
    bottom: 20px;
    right: 30px;
    z-index: 99;

    border: none;
    outline: none;

    background-color: #d1d1d1;
    /* Set a background color */
    color: #2157ad;
    /* Text color */

    opacity: 0.85;

    cursor: pointer;
    /* Add a mouse pointer on hover */

    padding: 12px;
    border-radius: 10px;
    font-size: 20px;
}